import react, { useState } from "react";
import noteContext from "./NoteApi";

const NoteState = (props)=>{
    const noteapi = {
        'app_name':'Textutils',
        'app_link':'About',
        'app_linked':'Home'
    }
    
    const [state, setstate] = useState(noteapi);
    
    const update=()=>{
    setTimeout(() => {
        setstate({
            'app_name':"Text_Utility",
            'app_link':"Contact",
            'app_linked':'Home'
        })
    }, 3000);
    setTimeout(() => {
        setstate({
            'app_name':"TextUtils",
            "app_link":"About",
            'app_linked':'Home'
        })
    }, 5000);
}
    return(
        <noteContext.Provider value={{state,update}}>
            {props.children}
        </noteContext.Provider>
    )
}

export default NoteState;