import './App.css';
import About from './components/About';
import Navbar from './components/Navbar';
import Textform from './components/Textform';
import React, { useState } from 'react'
import Alert from './components/Alert';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
} from "react-router-dom";
import NoteState from './components/StateApi';

function App(){
  const [dark, setdark] = useState({
    darkmod:'light',
    intext:"Enable Dark Mode"
  });
  const [alert, setalert] = useState(null);
  const showAlert=(message,type)=>{
    setalert({
    msg:message,
    Type:type
  })
  setTimeout(() => {
    setalert(null);
  }, 2000);
  }
  function navmod(){
    console.log('navmode is on');
    if(dark.darkmod === 'light'){
      document.body.style.backgroundColor="#042743"
      document.title = "TextUtilites - DarkMode"
      showAlert('Enabled dark Mode','success');
      setdark({
        darkmod:'dark',
        opmod:'light',
        intext:"Enable Light Mode"
        });
      }
    else {
      document.body.style.backgroundColor="white";
      document.title = "TextUtilies - LightMode"
      showAlert('Enabled Light Mode','success');
      setdark({
        darkmod:"light",
        opmod:'dark',
        intext:"Enable Dark Mode"
      });
      }
  }

  const customDark=(e)=>{
    let darkCol=e.target.style.backgroundColor;
    console.log(darkCol);
    document.body.style.backgroundColor=`${darkCol}`;
  }
  return (
    <NoteState>
    <div className="App">
     <Navbar title="TextUtils" dark={dark} text={dark.intext} darkmod={dark.darkmod} mode={navmod} customDark={customDark}/>
     <Alert alert={alert}/>
     <h1 className={`my-5 text-center text-${dark.opmod}`}>Create - Edit - Delete - Upper - LowerCase - And Modify Text.</h1>
     <Router>
     <Switch>
          <Route path="/about">
            <About dark={dark} />
          </Route>
          <Route path="/">
          <Textform dark={dark} showAlert={showAlert}/>
          </Route>
        </Switch>
        </Router>
    </div>
        </NoteState>
  );
}

export default App;
