import React from 'react'
import { useState } from 'react'

export default function Textform(props) {
    const [state, setstate] = useState('');
    let dis = {
        able:state.length===0?true:false
    }
    const upClick  = ()=>{
        let capital = state.toUpperCase();
        setstate(capital);
        props.showAlert('Converted to Uppercase','success');
    }
    const downClick  = ()=>{
        let lower = state.toLowerCase();
        setstate(lower);
        props.showAlert('Converted to Lowercase','success');
    }
    const handleOnChange = (e)=>{
        console.log("This is handle onchange");
        setstate(e.target.value);
    }
    const Capitalize = ()=>{
        const arr = state.split(" ");
        console.log(arr);


            //1st Method
            // for (var i = 0; i < arr.length; i++) {
            //     arr[i] = arr[i].charAt(0).toUpperCase() + arr[i].slice(1);
            // }
            // let arrcap=arr.join(" ");
            // setstate(arrcap);

            let capitalize="";
            arr.forEach((element)=>{
                console.log(element);
                let capital=element.charAt(0).toUpperCase()+element.slice(1);   
                capitalize+=capital+" ";
                setstate(capitalize);
            })
        props.showAlert('Capitalized your text','success');
        }
            const Copy = ()=>{
                let text = document.getElementById('exampleFormControlTextarea1');
                text.select();
                navigator.clipboard.writeText(text.value);
        props.showAlert('Copied your text to clipboard','success');
        }
            const Clear = ()=>{
                setstate('');
        }
            const Space = ()=>{
                let text = state.split(' ');
                setstate(text.join(''));
        props.showAlert('Removed spaces from your text','success');
            }
        return (
        <div>
        <div className="container" style={{color:props.dark.darkmod ==='dark'?'white':'black'}}>
            <div className="mb-3">
           <h3 className="my-5">Enter text to Analyze</h3>
            <textarea style={{backgroundColor:props.dark.darkmod==='dark'?"rgb(36 74 104)":"white",color:props.dark.darkmod==="dark"?"white":"black"}} className="form-control" onChange={handleOnChange} value={state} id="exampleFormControlTextarea1" rows="10"></textarea>
            </div>
            <button type="button" disabled={dis.able} onClick={upClick} className="btn btn-primary m-1 p-1">Convert to Uppercase</button>
            <button type="button" disabled={dis.able} onClick={downClick} className="btn btn-primary m-1 p-1">Convert to Lowercase</button>
            <button type="button" disabled={dis.able} onClick={Capitalize} className="btn btn-primary m-2 p-1">Convert to Capitalize</button>
            <button type="button" disabled={dis.able} onClick={Copy} className="btn btn-primary m-2 p-1">Copy Text</button>
            <button type="button" disabled={dis.able} onClick={Clear} className="btn btn-primary m-2 p-1">Clear Text</button>
            <button type="button" disabled={dis.able} onClick={Space} className="btn btn-primary m-2 p-1">Remove Every Space</button>
        </div>
        <div className="container my-3" style={{color:props.dark.darkmod ==='dark'?'white':'black'}}>
            <h1>Your text summary</h1>
            <p><b>{state.split(" ").filter((element)=>element.length!==0).length}{state.split(" ").filter((element)=>element.length!==0).length>1?' words ':' word '}</b> and {state.length}{state.length>1?' characters ':' character '}.</p>
            <p><i>{state.split(" ").filter((element)=>element.length!==0).length*0.006}{state.split(" ").filter((element)=>element.length!==0).length>1?' minutes ':' minute '}</i> to read.</p>
            <h3>Preview</h3>
            <p style={{color:state.length===0?'red':props.dark.darkmod==='dark'?'white':'black'}}>{state.length===0?"Write something to show preview":state}</p>
        </div>
        </div>
    )
}
