import React from 'react'
import { useContext } from 'react';
import { Link, BrowserRouter as Router} from 'react-router-dom'
import { useLocation } from 'react-router';
import '../App.css';
import noteContext from './NoteApi';
import { useEffect } from 'react';

export default function Navbar(props) {

    const bar = useContext(noteContext);

    // let location = useLocation();

    // useEffect(() => {
    //   console.log(location)
    // }, [location]);

      useEffect(() => {
      bar.update();
    }, []);

    return (
        <div>
            <nav className={`navbar navbar-expand-lg navbar-${props.darkmod} bg-${props.darkmod}`}>
  <div className="container-fluid">
    <Router>
    {/* <Link className="navbar-brand" to="/">{bar.state.app_name}</Link> */}
    <Link className="navbar-brand" to="/">Home</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mb-2 mb-lg-0">
        <li className="nav-item">
          {/* <Link className="nav-link active" to="/">{bar.state.app_linked}</Link> */}
          <Link className="nav-link active" to="/">Home</Link>
        </li>
          </ul>
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          {/* <Link className="nav-link" to="/about">{bar.state.app_link}</Link> */}
          <Link className="nav-link" to="/about">About</Link>
        </li>
          </ul>
        {props.dark.darkmod==='dark' && <div className='d-flex flex-wrap'>
      {/* <div className="btn btn-light mx-2 py-3 px-4">Choose color</div> */}
      {/* <div className="btn btn-primary mx-2 py-3 px-4"></div> */}
      {/* <div className="btn btn-danger mx-2 py-3 px-4"></div> */}
      {/* <div className="btn btn-success mx-2 py-3 px-4"></div> */}
      {/* <div className="btn btn-secondary mx-2 py-3 px-4"></div> */}
      <div className='text-light px-1 py-2'>Choose Color:</div>
      <div onClick={props.customDark} style={{backgroundColor:'#0069D9'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#563D7C'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#6C757D'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#28A745'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#DC3545'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#FFC107'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#17A2B8'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#FFFFFF'}} className='mystyle mx-1 my-1'></div>
      <div onClick={props.customDark} style={{backgroundColor:'#343A40'}} className='mystyle mx-1 my-1'></div>
      </div>}
      <div className={`form-check form-switch mx-3 text-${props.dark.darkmod==='light'?"dark":"light"}`}>
          <input className="form-check-input" onClick={props.mode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">{props.dark.intext}</label>
        </div>
    </div>
          </Router>
  </div>
</nav>
        </div>
    )
}
